@import url('https://fonts.googleapis.com/css2?family=Arsenal:ital,wght@0,400;0,700;1,400;1,700&family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
aside, nav, footer, header, section, article { display: block }
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
textarea{
	resize:none;
}
input, textarea { outline:none; }
input:active, textarea:active { outline:none; }
:focus { outline:none; }
body {
	margin:0;
	color:#000;
	font-family: 'Arsenal', sans-serif;
    font-weight: 400;
	font-size:14px;
	background:#fff;
	min-width:320px;
	height:100%;
	position:relative;
	-webkit-text-size-adjust: none;
}
*{
	box-sizing:border-box;
	-webkit-box-sizing:border-box;
	-moz-box-sizing:border-box;
	-o-box-sizing:border-box;
	-ms-box-sizing:border-box;
}
html, body{
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent;
    &:hover{
        background: none;
        -webkit-tap-highlight-color: rgba(0,0,0,0);
        -webkit-tap-highlight-color: transparent;
    }
    &:focus{
        background: none;
        -webkit-tap-highlight-color: rgba(0,0,0,0);
        -webkit-tap-highlight-color: transparent;
    }
}
@mixin after() {
    content:'';
    display:block;
    clear:both;
}
@mixin transition() {
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
#wrapper{
	overflow:hidden;
}
.fastisiPricePage {
    margin: 0 auto;
    padding: 50px 15px;
    max-width: 1224px;
    width: 100%;
    &_title {
        display: block;
        text-align: center;
        font-weight: 700;
        color: #000;
        max-width: 916px;
        margin: 0 auto 60px;
        font-size: 48px;
        line-height: 57px; 
        letter-spacing: -1.44px;
        position: relative;
        z-index: 2;
    }
    &_subtitle {
        display: block;
        text-align: center;
        color: #444;
        font-size: 22px;
        font-weight: 700;
        line-height: 33px;
        letter-spacing: -0.66px;
        max-width: 660px;
        margin: 0 auto;
        position: relative;
        z-index: 2;
    }
    &_description{
        margin-bottom: 125px;
    }
    &_soc {
        max-width: 962px;
        margin: 0 auto 125px;
        &-container {
            margin: 0 -5px;
            display: flex;
            justify-content: space-between;
        }
        &-link {
            width: 25%;
            padding: 0 5px;
            a {
                display: flex;
                align-items: center;
                justify-content: center;
                height: 70px;
                width: 100%;
                border-radius: 10px;
                @include transition;
                text-decoration: none;
                font-size: 19px;
                font-weight: 700;
                letter-spacing: -0.57px;
                text-transform: uppercase;
                padding: 0 15px;
                img {
                    display: block;
                    margin-right: 15px;
                }
                &.inst {
                    background: rgba(142, 43, 225, 0.05);
                    color: #6510AD;
                    &:hover {
                        background: rgba(142, 43, 225, 0.15);
                    }
                }
                &.telg {
                    background: rgba(42, 171, 238, 0.05);
                    color: #148DCC;
                    &:hover {
                        background: rgba(42, 171, 238, 0.15);
                    }
                }
                &.viber {
                    background: rgba(115, 96, 242, 0.05);
                    color: #5541D9;
                    &:hover {
                        background: rgba(115, 96, 242, 0.15);
                    }
                }
                &.tel {
                    background: rgba(43, 225, 164, 0.05);
                    color: #006140;
                    &:hover {
                        background: rgba(43, 225, 164, 0.15);
                    }
                }
            }
        }
    }
    &_info{
        margin-bottom: 125px;
        .fastisiPricePage_title {
            margin-bottom: 45px;
        }
        .fastisiPricePage_subtitle {
            margin-bottom: 60px;
        }
        &-items{
            display: flex;
            justify-content: space-between;
            margin: 0 -2.5px; 
        }
        &-item {
            width: 33.3%;
            padding: 0 2.5px;
            &_body {
                border-radius: 10px;
                border: 2px solid #E6EDF0;
                background: #FAFBFC;
                display: flex;
                padding: 45px 45px;
                position: relative;
                &:after {
                    @include after;
                    position: absolute;
                    top: -2px;
                    left: 45px;
                    width: 60px;
                    height: 2px;
                    background: #D30D0D;
                }
            }
            &:nth-child(2) {
                .fastisiPricePage_info-item_body:after {
                    background: #00AD3A;
                }
            }
            &:nth-child(3) {
                .fastisiPricePage_info-item_body:after {
                    background: #F90;
                }
            }
            p {
                display: block;
                color: #000;
                font-size: 19px;
                font-weight: 700;
                line-height: 26px;
                letter-spacing: -0.57px;
            }
        }
    }
    &_presentation{
        margin: 0 auto;
        max-width: 1070px;
        width: 100%;
        .fastisiPricePage_title {
            margin-bottom: 45px;
        }
        .fastisiPricePage_subtitle {
            margin-bottom: 150px;
        }
        &-container {
            border-radius: 26px;
            border: 2px solid #E6EDF0;
            padding: 175px 62px 40px 62px;
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 170px;
            &:last-child {
                margin-bottom: 0;
            }
            .fastisiPricePage_title {
                margin-bottom: 40px;
            }
            &.openPrice {
                .fastisiPricePage_presentation-price_tab-body_tr {
                    display: flex;
                    &:first-child {
                        .fastisiPricePage_presentation-price_tab-body_td {
                            &:nth-child(1) {
                                border-radius: 0;
                                border-bottom: none;
                            }
                            &:nth-child(2) {
                                border-radius: 0;
                                border-bottom: none;
                            }
                            &:nth-child(3) {
                                border-radius: 0;
                                border-bottom: none;
                            }
                            &:nth-child(4) {
                                border-radius: 0;
                                border-bottom: none;
                            }
                        }
                    }
                }
            }
            &.openFotos {
                .fastisiPricePage_presentation-foto_item {
                    display: block;
                }
                .fastisiPricePage_presentation-foto_btn {
                    display: none;
                }
            }
            &.openPrice {
                .fastisiPricePage_allBtn {
                    background-color: #fff;
                    color: #F27D00;
                    i {
                        background: url(../img/arrow-orange.svg) no-repeat;
                    }
                    &:hover {
                        background: #F27D00;
                        color: #fff;
                        i {
                            background: url(../img/arrow-top-white.svg) no-repeat;
                        }
                    }
                }
            }
        }
        &-buttons{
            padding: 0 15px;
            display: flex;
            width: 100%;
        }
        &-img{
            position: absolute;
            width: 28%;
            left: 50%;
            transform: translate(-50%, 0);
            top: -150px;
            height: 300px;
            display: flex;
            align-items: center;
            img {
                display: block;
                width: 100%;
                height: auto;
            }
        }
        &-items{
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 60px;
        }
        &-item{
            margin: 0 23px;
            display: flex;
            align-items: center;
            img {
                display: block;
                min-width: 40px;
                margin-right: 10px;
            }
            span {
                display: block;
                color: rgba(0,0,0, 0.35);
                font-size: 14px;
                font-weight: 700;
                letter-spacing: -0.42px;
                text-transform: uppercase;
                strong {
                    display: block;
                    color: #000;
                    font-size: 26px;
                    font-weight: 700;
                    letter-spacing: -0.78px;
                    text-transform: uppercase;
                }
            }
        }
        &-info{
            border-radius: 8px;
            background: #FCF9ED;
            padding: 15px 25px;
            margin-bottom: 60px;
            img {
                display: block;
                min-width: 30px;
                margin-right: 15px;
            }
            display: flex;
            align-items: center;
            color: #E57300;
            font-size: 19px;
            font-weight: 700;
            letter-spacing: -0.57px;
            text-transform: uppercase;
        }
        &-foto{
            width: 100%;
            padding: 0 18px;
            display: flex;
            flex-direction: column;
            align-items: center;
            &_btn{
                display: flex;
                align-items: center;
                justify-content: center;
                height: 60px;
                padding: 0 24px;
                border-radius: 10px;
                border: 2px solid rgba(85, 112, 123, 0.10);
                background: rgba(85, 112, 123, 0.05);
                @include transition;
                cursor: pointer;
                color: #55707B;
                font-size: 19px;
                font-style: normal;
                font-weight: 700;letter-spacing: -0.57px;
                text-transform: uppercase;
                i {
                    display: block;
                    width: 12px;
                    height: 12px;
                    margin-left: 12px;
                    background: url(../img/arrow.svg) no-repeat;
                }
                &:hover {
                    background: rgba(85, 112, 123, 0.15);
                }
            }
            &_items{
                display: flex;
                flex-wrap: wrap;
                margin: 0 -2.5px 20px;
                width: 100%;
            }
            &_item {
                width: 33.3%;
                padding: 0 2.5px;
                margin-bottom: 5px;
                display: none;
                &:nth-child(1) {
                    display: block;
                }
                &:nth-child(2) {
                    display: block;
                }
                &:nth-child(3) {
                    display: block;
                }
            }
            &_img {
                border-radius: 4px;
                border: 2px solid #E6EDF0;
                background: #FFF;
                img {
                    display: block;
                    width: 100%;
                    height: auto;
                }
            }
            margin-bottom: 70px;
        }
        &-price{
            width: 100%;
            margin-bottom: 35px;
            &_tab{
                &-header{
                    width: 100%;
                    background: #F90;
                    display: flex;
                    border-radius: 16px 16px 0 0;
                    overflow: hidden;
                    &_td {
                        display: flex;
                        align-items: center;
                        height: 64px;
                        width: 100%;
                        justify-content: center;
                        &:nth-child(1) {
                            border-radius: 16px 0px 0px 0px;
                            border-right: 2px solid #F58F00;
                            border-left: 2px solid #F90;
                            min-width: 139px;
                        }
                        &:nth-child(2) {
                            border-right: 2px solid #FFB71B;
                            min-width: 139px;
                        }
                        &:nth-child(3) {
                            background: #FFB71B;
                            border-right: 2px solid #FFB71B;
                            min-width: 314px;
                            span {
                                &:after {
                                    @include after;
                                    position: absolute;
                                    top: 50%;
                                    transform: translate(0, -50%);
                                    width: 85px;
                                    height: 2px;
                                    background-color: #FFCD62;
                                    left: -105px;
                                }
                                &:before {
                                    @include after;
                                    position: absolute;
                                    top: 50%;
                                    transform: translate(0, -50%);
                                    width: 85px;
                                    height: 2px;
                                    background-color: #FFCD62;
                                    right: -105px;
                                }
                            }
                        }
                        &:nth-child(4) {
                            border-radius: 0px 16px 0px 0px;
                            min-width: 350px;
                            border-right: 2px solid #F90;
                            span {
                                &:after {
                                    @include after;
                                    position: absolute;
                                    top: 50%;
                                    transform: translate(0, -50%);
                                    width: 85px;
                                    height: 2px;
                                    background-color: #FFB342;
                                    left: -105px;
                                }
                                &:before {
                                    @include after;
                                    position: absolute;
                                    top: 50%;
                                    transform: translate(0, -50%);
                                    width: 85px;
                                    height: 2px;
                                    background-color: #FFB342;
                                    right: -105px;
                                }
                            }
                        }
                    }
                    span {
                        display: block;
                        color: #FFF;
                        font-size: 17px;
                        font-weight: 800;
                        font-family: 'Inter', sans-serif;
                        letter-spacing: -0.51px;
                        text-transform: uppercase;
                        position: relative;
                    }
                }
                &-body{
                    &_tr{
                        display: flex;
                        overflow: hidden;
                        &:nth-child(even) {
                            .fastisiPricePage_presentation-price_tab-body_td {
                                &:nth-child(1) {
                                    background: #FAFAFA;
                                }
                                &:nth-child(2) {
                                    background: #F0FAF5;
                                    color: #179958;
                                }
                                &:nth-child(3) {
                                    background: #FCF9ED;
                                }
                                &:nth-child(4) {
                                    background: #F0FAF5;
                                    span {
                                        color: #179958;
                                        b {
                                            color: #74B293;
                                        }
                                    }
                                }
                            }
                        }
                        &:last-child {
                            .fastisiPricePage_presentation-price_tab-body_td {
                                &:nth-child(1) {
                                    border-radius: 0 0 0 16px;
                                    border-bottom: 2px solid #E6EDF0;
                                }
                                &:nth-child(2) {
                                    border-bottom: 2px solid #E6EDF0;
                                }
                                &:nth-child(3) {
                                    border-bottom: 2px solid rgba(243, 237, 214, 0.75);
                                }
                                &:nth-child(4) {
                                    border-radius: 0 0 16px 0;
                                    border-bottom: 2px solid #E6EDF0;
                                }
                            }
                        }
                        display: none;
                        &:first-child {
                            display: flex;
                            .fastisiPricePage_presentation-price_tab-body_td {
                                &:nth-child(1) {
                                    border-radius: 0 0 0 16px;
                                    border-bottom: 2px solid #E6EDF0;
                                }
                                &:nth-child(2) {
                                    border-bottom: 2px solid #E6EDF0;
                                }
                                &:nth-child(3) {
                                    border-bottom: 2px solid rgba(243, 237, 214, 0.75);
                                }
                                &:nth-child(4) {
                                    border-radius: 0 0 16px 0;
                                    border-bottom: 2px solid #E6EDF0;
                                }
                            }
                        }
                    }
                    &_td {
                        display: flex;
                        align-items: center;
                        height: 60px;
                        justify-content: space-around;
                        width: 100%;
                        &:nth-child(1) {
                            border-right: 2px solid #E6EDF0;
                            border-left: 2px solid #E6EDF0;
                            min-width: 139px;
                            color: #555859;
                            font-family: 'Inter', sans-serif;
                            font-size: 17px;
                            font-weight: 500;
                            letter-spacing: -0.51px;
                        }
                        &:nth-child(2) {
                            border-right: 2px solid rgba(243, 237, 214, 0.75);
                            min-width: 139px;
                            color: #555859;
                            font-family: 'Inter', sans-serif;
                            font-size: 20px;
                            font-weight: 500;
                            letter-spacing: -0.6px;
                        }
                        &:nth-child(3) {
                            border-right: 2px solid rgba(243, 237, 214, 0.75);
                            min-width: 314px;
                            span {
                                display: flex;
                                align-items: flex-start;
                                color: #393E40;
                                font-family: 'Inter', sans-serif;
                                font-size: 22px;
                                font-weight: 600;
                                letter-spacing: -0.66px;
                                b {
                                    display: block;
                                    margin-left: 4px;
                                    color: #938B7F;
                                    font-size: 14px;
                                    font-weight: 700;
                                    letter-spacing: -0.42px;
                                }
                            }
                        }
                        &:nth-child(4) {
                            border-right: 2px solid #E6EDF0;
                            min-width: 350px;
                            span {
                                display: flex;
                                align-items: flex-start;
                                color: #393E40;
                                font-family: 'Inter', sans-serif;
                                font-size: 22px;
                                font-weight: 600;
                                letter-spacing: -0.66px;
                                b {
                                    display: block;
                                    margin-left: 4px;
                                    color: #938B7F;
                                    font-size: 14px;
                                    font-weight: 700;
                                    letter-spacing: -0.42px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    &_allBtn{
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        background: #F27D00;
        border: 2px solid #F27D00;
        height: 60px;
        padding: 0 15px;
        color: #FFF;
        font-size: 19px;
        font-weight: 700;
        letter-spacing: -0.57px;
        text-transform: uppercase;
        text-decoration: none;
        cursor: pointer;
        @include transition;
        margin-right: 5px;
        width: 100%;
        white-space: nowrap;
        i {
            display: block;
            background: url(../img/arrow-white.svg) no-repeat;
            width: 12px;
            height: 12px;
            margin-left: 12px;
        }
        &:hover {
            background-color: #fff;
            color: #F27D00;
            i {
                background: url(../img/arrow-bot-orange.svg) no-repeat;
            }
        }
        &.allBtn2 {
            background-color: #fff;
            color: #F27D00;
            i {
                background: url(../img/arrow-orange.svg) no-repeat;
            }
            &:hover {
                background: #F27D00;
                color: #fff;
                i {
                    background: url(../img/arrow-top-white.svg) no-repeat;
                }
            }
        }
    }
    &_cardProductBtn {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        background: #00AD3A;
        border: 2px solid #00AD3A;
        height: 60px;
        padding: 0 15px;
        color: #FFF;
        font-size: 19px;
        font-weight: 700;
        letter-spacing: -0.57px;
        text-transform: uppercase;
        text-decoration: none;
        cursor: pointer;
        @include transition;
        margin-right: 5px;
        width: 100%;
        white-space: nowrap;
        &:hover {
            background-color: #fff;
            color: #00AD3A;
        }
    }
    &_videoBtn{
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        background: #E32D2D;
        border: 2px solid #E32D2D;
        height: 60px;
        padding: 0 15px;
        color: #FFF;
        font-size: 19px;
        font-weight: 700;
        letter-spacing: -0.57px;
        text-transform: uppercase;
        text-decoration: none;
        cursor: pointer;
        @include transition;
        margin-right: 5px;
        width: 100%;
        white-space: nowrap;
        i {
            display: block;
            background: url(../img/icon-play.svg) no-repeat;
            width: 32px;
            height: 32px;
            margin-right: 12px;
        }
        &:hover {
            background-color: #fff;
            color: #E32D2D;
            i {
                background: url(../img/icon-play-red.svg) no-repeat;
            }
        }
    }
    &_chatTelgBtn {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        background: #fff;
        border: 2px solid #0AA4E7;
        height: 60px;
        padding: 0 15px;
        min-width: 118px;
        width: 118px;
        color: #0AA4E7;
        font-size: 19px;
        font-weight: 700;
        letter-spacing: -0.57px;
        text-transform: uppercase;
        text-decoration: none;
        cursor: pointer;
        @include transition;
        margin-right: 5px;
        white-space: nowrap;
        i {
            display: block;
            background: url(../img/icon-telg.svg) no-repeat;
            width: 30px;
            height: 30px;
            margin-right: 12px;
        }
        &:hover {
            background-color: #e9f8ff;
        }
    }
    &_chatInstBtn {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        background: #fff;
        border: 2px solid #872BEB;
        height: 60px;
        padding: 0 15px;
        min-width: 118px;
        width: 118px;
        color: #872BEB;
        font-size: 19px;
        font-weight: 700;
        letter-spacing: -0.57px;
        text-transform: uppercase;
        text-decoration: none;
        cursor: pointer;
        @include transition;
        white-space: nowrap;
        i {
            display: block;
            background: url(../img/icon-inst.svg) no-repeat;
            width: 30px;
            height: 30px;
            margin-right: 12px;
        }
        &:hover {
            background-color: #f5ecff;
        }
    }
}













/*media*/
@media screen and (max-width: 1100px) {
    .fastisiPricePage_presentation-container {
        padding-left: 15px;
        padding-right: 15px;
    }
    .fastisiPricePage_presentation-price_tab-header_td:nth-child(1) {
        min-width: 120px;
        max-width: 120px;
    }
    .fastisiPricePage_presentation-price_tab-body_td:nth-child(1) {
        min-width: 120px;
        max-width: 120px;
    }
    .fastisiPricePage_presentation-price_tab-header_td:nth-child(2) {
        min-width: 120px;
        max-width: 120px;
    }
    .fastisiPricePage_presentation-price_tab-body_td:nth-child(2) {
        min-width: 120px;
        max-width: 120px;
    }
